import { createStore } from 'vuex'
import { Add, Buttons } from '@/services/api.js'

export default createStore({
  state() {
    return {
      task: null,
      botoes: null,
      currentComponent: null,
    }
  },
  getters: {
    getTask(state) {
      return state.task
    },
    getBotoes(state) {
      return state.botoes
    },
    getCurrentComponent(state) {
      return state.currentComponent
    }
  },
  mutations: {
    setTask(state, data) {
      state.task = data
    },
    setBotoes(state, data) {
      state.botoes = data
    },
    setCurrentComponent(state, data) {
      state.currentComponent = data
    }
  },
  actions: {
    async fetchAddTask({ commit }, payload) {
      const response = await Add.get()
      commit('setTask', response.data)
    },
    async fetchBotoes({ commit }) {
      const response = await Buttons.get()
      commit('setBotoes', response.data)
    },
    async fetchCurrentComponent({ commit }, payload) {
      commit('setCurrentComponent', payload)
    }
  }
})
